.movie_sliders_container{
  width: 100%;
  position: relative;
  height: 400px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.slider_couple_container{
  width: 100%;
  position: relative;
  height: initial;

}
.slider_second_1,
.slider_second_2,
.slider_second_3,
.slider_second_4{
  width: 50%;
  position: relative;
  height: 200px;
  overflow: hidden;
  float: left;
  /* box-shadow: inset 0 0 10px #000000; */
}
.slider_second_1:after,
.slider_second_2:after,
.slider_second_3:after,
.slider_second_4:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    -webkit-box-shadow: inset 0px 0px 10px 0 #000000;
            box-shadow: inset 0px 0px 10px 0 #000000;
}

.slider_second_1_img,
.slider_second_2_img,
.slider_second_3_img,
.slider_second_4_img{
  width: 100%;
}



