.separator_wraper{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  margin: 30px 0;
}
.separator{
  width: 80%;
  position: relative;
}
.separator::before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;border-radius: 2px;
  height: 2px;
  background:-webkit-gradient(linear, left top, right top, from(rgb(105, 206, 172)), color-stop(50%, rgb(249, 51, 41)), to(rgb(235, 164, 2)));
  background:-webkit-linear-gradient(left, rgb(105, 206, 172) 0%, rgb(249, 51, 41) 50%, rgb(235, 164, 2) 100%);
  background:-o-linear-gradient(left, rgb(105, 206, 172) 0%, rgb(249, 51, 41) 50%, rgb(235, 164, 2) 100%);
  background:linear-gradient(to right, rgb(105, 206, 172) 0%, rgb(249, 51, 41) 50%, rgb(235, 164, 2) 100%);
  -webkit-box-shadow: 0 0 2px rgba(0, 0, 0, 0.33);
          box-shadow: 0 0 2px rgba(0, 0, 0, 0.33);
}
.dot_separator{
  position: relative;
  width: 20px;
  height: 20px;
}
.dot_separator::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    border-radius: 50%;
    height: 100%;
    background:-webkit-linear-gradient(45deg, rgb(105, 206, 172) 0%, rgb(249, 51, 41) 50%, rgb(235, 164, 2) 100%);
    background:-o-linear-gradient(45deg, rgb(105, 206, 172) 0%, rgb(249, 51, 41) 50%, rgb(235, 164, 2) 100%);
    background:linear-gradient(45deg, rgb(105, 206, 172) 0%, rgb(249, 51, 41) 50%, rgb(235, 164, 2) 100%);
    -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.43);
            box-shadow: 0 1px 4px rgba(0, 0, 0, 0.43);
}
