@import url('https://fonts.googleapis.com/css?family=Roboto|Bungee');
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-family: 'Roboto', sans-serif;
  background: #000000;
  color: white;
  background: -webkit-linear-gradient(top, #000000 0%,#601a1a 100%);
  background: -webkit-gradient(linear, left top, left bottom, from(#000000),to(#601a1a));
  background: -o-linear-gradient(top, #000000 0%,#601a1a 100%);
  background: linear-gradient(to bottom, #000000 0%,#601a1a 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#601a1a',GradientType=0 );
}
*{
  outline: none;
}
html{
  -webkit-font-smoothing: antialiased;
}



